<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="Company" SubModuleCode="" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="COMPANY_ID" :listType="['List']" :ListData="CompanyData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import Schema from "./Schema"
export default {
    components: { },
    data() {
        return {
            isLoading: false,
            CompanyName:'',
            CompanyAdmin:'',
            EmailId:'',
            Address: '',
            PhoneNo:'',
            CompanyData: [],
            SortBy: '',
            SortExp: '',
            noRecords: '',
            PageSize: "10",
            PageNumber: "1",
            Headers: [{
                "COLUMN_NAME": "ROWNUMBER",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": '#',
                "DISPLAY_ORDER": 1,

            },{
                "COLUMN_NAME": "COMPANY_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Company Name',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "EMAIL_ID",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Email',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "ADDRESS",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Address',
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "PHONE",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Phone',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "PROJECTS",
                "DATA_TYPE": "Date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": '# Projects',
                "DISPLAY_ORDER": 5,
            },

            ],
            leftSearchFields: [{
                fieldName: 'Search BY Company Name',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            UserType:'',
            UserId:'',
        }
    },
    created: async function () {
        var vm = this;
        debugger
        await vm.FetchData();
        vm.UserId = this.GetUserInfo.ID;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.IsDefault=vm.GetUserInfo.IsDefault;
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('Companies');
            divElement[0].appendChild(spanElement);
        }
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined' && value.searchCondition !='') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.CompanyName = result;
            }
            else {
                this.CompanyName = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) { },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Active':
                        if(row.STATUS_ID==1001){
                            action.isVisible = true;
                        }else{
                            action.isVisible = false;
                        }
                        break;
                    case 'InActive':
                        if(row.STATUS_ID!=1001){
                            action.isVisible = true;
                        }else{
                            action.isVisible = false;
                        }
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick: function (callbackfunction, item, index, event) { },
        FetchData: async function () {
            var vm = this;
            var url = `companyname=${vm.CompanyName}&companyadmin=${vm.CompanyAdmin}&email=${vm.EmailId}&address=${vm.Address}&phone=${vm.PhoneNo}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`
            await DataServices.GetCompanyListing(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.CompanyData = [];
                    vm.CompanyData = response.data;
                    vm.TotalRecords = response.data[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                } else {
                    vm.CompanyData = [];
                    vm.TotalRecords = 0
                    vm.noRecords = true;
                }
            })
        }
    }
}
</script>
